body {
    background-color: #555 !important;
    background-image: radial-gradient( #666, #455);
    -webkit-box-shadow: 14px 10px 413px 3px rgba(0, 0, 0, 0.6);
    -moz-box-shadow: 14px 10px 43px 3px rgba(0, 0, 0, 0.6);
    box-shadow: 14px 10px 43px 3px rgba(0, 0, 0, 0.6);
}

.App {
    text-align: center;
    color: #ccc;
}

.App .app-container {
    margin: 0 0 !important;
}

.App .pane {
    background-color: #555;
    /* border: 1px solid #faa; */
    background-image: linear-gradient( #777, #555);
    -webkit-box-shadow: 14px 10px 413px 3px rgba(0, 0, 0, 0.6);
    -moz-box-shadow: 14px 10px 43px 3px rgba(0, 0, 0, 0.6);
    box-shadow: 14px 10px 43px 3px rgba(0, 0, 0, 0.6);
}

.App .row .pane-title {
    text-transform: uppercase;
    padding: 0 1rem;
    margin-bottom: 0.5rem;
    background-color: #f55;
    text-align: left;
    border-radius: 5px 5px 0 0;
}

.App .container {
    margin: 10px 0;
    padding-bottom: 10px;
    border-radius: 5px;
    /* padding-top: 10px; */
}

.Planner {
    font-size: 80%;
    /* background-color: #777; */
}

.Planner .locked {
    background-color: #6c757d;
}

.Planner .step {
    cursor: pointer;
    background-color: #6c757d40;
    margin-bottom: 1px;
}

.Planner .current-step {
    background-color: #f55;
}

.SimpleProgress .bar {
    padding: 1px;
    background-color: #f55;
}

.ModePanel input {
    margin: 0 5px;
    /* width: 4rem; */
    display: inline;
}

.ModePanel .pane-body div {
    margin: 5px 0;
}

.SimpleVis {
    position: relative;
}

.SimpleVis li {
    width: 2rem;
    height: 2rem;
    text-align: center;
    list-style: none;
    border: 1px dashed;
    border-radius: 100%;
}

.SimpleVis .active {
    border: 1px solid #f55;
    color: #f55;
    text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #e60073, 0 0 40px #e60073, 0 0 50px #e60073, 0 0 60px #e60073, 0 0 70px #e60073;
}

.PresetsManager .preset {
    font-size: 0.8rem;
    cursor: pointer;
    border: 1px solid #f55;
    border-collapse: collapse;
}